.container-xl,
.container-lg,
.container-md,
.container-sm {
  width: 100%;
  padding-right: var(--grid-padding-x, 0.75rem);
  padding-left: var(--grid-padding-x, 0.75rem);
  margin-right: auto;
  margin-left: auto;
}

