.row {
  // --grid-padding-x: 1.5rem;
  --grid-padding-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--grid-padding-y) * -1);
  margin-right: calc(var(--grid-padding-x) * -.5);
  margin-left: calc(var(--grid-padding-x) * -.5);
}

.row>* {
  box-sizing: border-box;
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--grid-padding-x) * .5);
  padding-left: calc(var(--grid-padding-x) * .5);
  margin-top: var(--grid-padding-y);
}


