/* ---------------------------------------------------------------------------------------------------------------------
МОДАЛЬНОЕ ОКНО С СИНИМИ ПЛИТКАМИ НА 7 ДНЕЙ 
-----------------------------------------------------------------------------------------------------------------------*/
.modal {
	&-grid {
		// ПРОТОТИП СЕКТИ - 1
		&.grid-proto-1 {
			grid-template-columns: 10fr 10fr 10fr 15fr;
			grid-template-rows: 10fr 10fr;
			grid-template-areas:
				"d1 d2 d3 d7"
				"d4 d5 d6 d7";

			@media (max-width: 650px) {
				grid-template-columns: 10fr 10fr 10fr;
				grid-template-rows: 10fr 10fr 10fr;
				grid-template-areas:
					"d1 d2 d3"
					"d4 d5 d6"
					"d7 d7 d7";
			}

			@media (max-width: 500px) {
				grid-template-columns: 10fr 10fr;
				grid-template-rows: 10fr 10fr 10fr 10fr;
				grid-template-areas:
					"d1 d2"
					"d3 d4"
					"d5 d6"
					"d7 d7";
			}

			.d1 {
				grid-area: d1;
			}
			.d2 {
				grid-area: d2;
			}
			.d3 {
				grid-area: d3;
			}
			.d4 {
				grid-area: d4;
			}
			.d5 {
				grid-area: d5;
			}
			.d6 {
				grid-area: d6;
			}
			.d7 {
				grid-area: d7;
			}
		}

		// ОДНА ПЛИТКА
		&_button {		
			z-index: 10;	
			// ФИОЛЕТОВЫЕ ЛУЧИ
			.modalBtn-light-violet {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 2;
				background-repeat: no-repeat;
				background-image: url("../../images/glow/rotate-violet.png");
				background-size: 100% 100%;
				transform: scale(1.35);
			}
			// ВЕРХНИЙ ТЕКСТ
			.modalBtn-day {
				align-self: start;

				display: block;
				position: relative;
				z-index: 3;
				text-transform: uppercase;
				color: #70fdff;
				font-size: 1.2rem;

				top: 7px;
				left: 10px;

				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);
			}

			// НИЖНИЙ ТЕКСТ
			.modalBtn-bottomText {
				align-self: center;

				display: block;
				text-align: center;
				position: relative;
				z-index: 3;
				color: #fff;
				font-size: 1.4rem;
				font-weight: 500;

				bottom: 11px;

				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

				&.bottomText-special {
					font-size: 1.7rem;
					bottom: 20px;
				}
			}

			// ПОДСКАЗКА ЗАВТРА
			.modalBtn-tommorow {
				display: none;
				position: absolute;
				z-index: 5;
				padding-top: 7px;

				text-align: center;
				color: black;

				top: -36px;
				left: 0;
				width: 100%;

				font-size: 0.8rem;
				line-height: 100%;

				min-height: 40px;

				background-image: url("../../images/tommorow.png");
				background-repeat: no-repeat;
				background-size: 100% 100%;
			}

			// АКТИВНАЯ ПЛИТКА
			&.current {
				transition: all 0.3s ease-in-out;
				z-index: 10;
				&:hover {
					transform: scale(1.02);
				}

				&::after {
					position: absolute;
					content: "";
					top: 10%;
					left: -2%;
					right: -2%;
					bottom: 10%;
					background-repeat: no-repeat;
					background-image: url("../../images/stars.png");
					background-size: 100% 100%;
					transform: scale(1.35);
				}

				&::before {
					position: absolute;
					content: "";
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					background-repeat: no-repeat;
					background-image: url("../../images/glow/rotate-blue.png");
					background-size: 100% 100%;
					transform: scale(2);
					z-index: 0;
				}

				box-shadow: 0px 0px 1px 2px rgb(255, 255, 255);

				.modalBtn-tommorow {
					display: block;
				}
			}
		}
	}
}
