/* ---------------------------------------------------------------------------------------------------------------------
МОДАЛЬНОЕ ОКНО С ФИОЛЕТОВОЙ, ЗЕЛЕНЕНЬКОЙ И НАБОРОМ ЖЕЛТЫХ ПЛИТОК
-----------------------------------------------------------------------------------------------------------------------*/

.modal {
	&-grid {
		// ПРОТОПИТ СЕТКИ ДЛЯ ВТОРОГО ОКНА
		&.grid-proto-2 {
			grid-template-columns: 1fr 1fr 1fr 1fr;
			grid-template-rows: 1fr 1fr;
			grid-template-areas:
				"w1 w2 w3 w3"
				"w1 w2 w3 w3";

			@media (max-width: 650px) {
				grid-template-columns: 1fr 1fr 1fr;
				grid-template-rows: 1fr 1fr 1fr;
				grid-template-areas:
					"w1 w3 w3"
					"w1 w3 w3"
					"w2 w2 w2";
			}

			@media (max-width: 500px) {
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 1fr 1fr 1fr 1fr;
				grid-template-areas:
					"w1 w1"
					"w2 w2"
					"w3 w3"
					"w3 w3";
			}

			.w1 {
				grid-area: w1;
			}
			.w2 {
				grid-area: w2;
			}
			.w3 {
				grid-area: w3;
			}

			.w1 {
				// background-color: #9534ef;
				& > img.modalBtn-image {
					transform: scale(1.6);
				}
			}
			.w2 {
				// background-color: #00b271;
				& > img.modalBtn-image {
					transform: scale(1.2);
				}
			}

			.w3 {
				background-color: rgb(0, 0, 0, 0.15);
			}
		}
		/*---------------------------------------------------------------------------------------------------------------------
// ФИОЛЕТОВАЯ И ЗЕЛЕНАЯ ПЛИТКИ
-----------------------------------------------------------------------------------------------------------------------*/

		&_button {
			position: relative;
			border-radius: 8px;
			border: 1px solid #000;

			min-height: 151px;
			padding: 0;

			cursor: pointer;

			display: flex;
			flex-direction: column;
			justify-content: space-between;

			background: rgb(100, 100, 100, 0.5);

			// РАСПЕРЕДЕЛЕНИЕ ПОЛОС ПО ВЕРТИКАЛИ (ВВЕРХ и ВНИХ)
			.space-between {
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
			}

			// ИЗОБРАЖЕНИЕ
			.modalBtn-image {
				position: absolute;
				inset-block-start: 20%;
				inset-inline-start: 20%;
				object-fit: contain;
				block-size: 60%;
				inline-size: 60%;
				z-index: 3;

				&.image-adaptive-500 {
					@include m500 {
						inset-inline-start: 45%;
						inline-size: 40%;
					}
				}

				&.image-adaptive-650 {
					@include m650 {
						inset-inline-start: 45%;
						inline-size: 40%;
					}
				}
			}

			// БЭДЖИК
			.modalBtn-badge {
				position: absolute;
				top: -8px;
				left: -8px;
				width: 45px;
				height: 45px;

				z-index: 15;
			}

			// КНОПКА ИНФОРМАЦИЯ
			.modalBtn-info {
				position: absolute;
				top: 45px;
				right: 10px;

				width: 22px;
				height: 22px;

				z-index: 15;
			}

			// СТРОЧКА С ТЕКСТОМ
			.modalBtn-ribbon {
				display: flex;
				align-items: center;
				justify-content: center;

				position: absolute;
				bottom: 32px;
				left: -10px;

				width: 100px;
				height: 40px;

				background-image: url("../../images/ribbon.png");
				background-repeat: no-repeat;
				background-size: 100% 100%;

				z-index: 15;
				span {
					color: white;
					font-size: 1.1rem;
					text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
					-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);
				}
			}

			// ПРОСТОЙ ТЕКСТ ВВЕРХУ КНОПКИ (НАД ИЗОБРАЖЕНИЕМ)
			.modalBtn-about {
				position: absolute;
				top: 45px;
				left: 50%;
				width: 90%;

				line-height: 100%;
				letter-spacing: 0;

				text-transform: uppercase;

				transform: translateX(-50%);
				z-index: 5;

				font-size: 1.4rem;
				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

				@include md {
					font-size: 1.1rem;
				}

				@include sm {
					font-size: 1rem;
				}

				// Сдвигание при адаптиве на 500пкс
				&.about-adaptive-500 {
					@include m500 {
						text-align-last: left;
						padding-left: 12px;
					}
				}

				// Сдвигание при адаптиве на 650пкс
				&.about-adaptive-650 {
					@include m650 {
						text-align-last: left;
						padding-left: 12px;
					}
				}
			}

			// ПРОСТОЙ ТЕКСТ ВНИЗУ КНОПКИ (ПОД ИЗОБРАЖЕНИЕМ)
			.modalBtn-under {
				position: absolute;
				top: 68%;
				left: 50%;
				width: 90%;

				transform: translateX(-50%);
				z-index: 5;

				line-height: 100%;
				font-size: 1.1rem;
				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

				// Сдвигание при адаптиве на 650пкс
				&.under-adaptive-650 {
					@include m650 {
						width: 50%;
						top: 37%;
						left: -4%;
						transform: translateX(0);
					}
				}
			}

			// ГРУППА ВНИЗУ КНОПКИ КРИСТАЛ И ТЕКСТ (ПОД ИЗОБРАЖЕНИЕМ)
			.modalBtn-gemUnder {
				display: flex;
				flex-direction: row;
				align-items: center;
				gap: 7px;

				position: absolute;
				top: 73%;
				left: 50%;

				transform: translateX(-50%);
				z-index: 5;

				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

				.gemUnder-image {
					position: relative;
					width: 30px;
					height: 30px;
				}
				.gemUnder-group {
					display: flex;
					flex-direction: column;
					align-items: start;
					line-height: 150%;

					&_min {
						font-size: 1.2rem;
					}
					&_max {
						font-size: 1.6rem;
					}
				}

				// Сдвигание при адаптиве на 500пкс
				&.gemUnder-adaptive-500 {
					@include m500 {
						top: 40%;
						right: -30%;
					}
				}
			}

			// Подсветка КРУТЯШИЕСЯ ЖЕЛТЫЕ
			.modalBtn-rotate-styles {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 2;

				// Изображение вложенной плитки
				img {
					position: absolute;
					inset-block-start: 0%;
					inset-inline-start: 0%;
					object-fit: contain;
					block-size: 100%;
					inline-size: 100%;
					z-index: 0;
				}
			}
		}

		&_topline {
			position: relative;
			z-index: 5;

			padding-left: 5px;
			padding-right: 5px;

			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;

			background-color: rgb(0, 0, 0, 0.15);

			min-height: 35px;
			span {
				color: white;
				font-size: 0.6rem;
			}
			@media (max-width: 650px) {
				span {
					font-size: 0.75rem;
				}
			}
		}

		&_bottomline {
			position: relative;
			z-index: 5;

			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;

			background-color: rgb(0, 0, 0, 0.15);

			padding-top: 9px;
			padding-bottom: 9px;

			border-radius: 0 0 8px 8px;

			text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
			-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

			&.green {
				background-color: rgb(51, 250, 6, 0.5) !important;
			}
			&.violet {
				background-color: rgb(255, 0, 0, 0.15) !important;
			}

			img {
				width: 28px;
				height: 28px;
			}

			span {
				color: white;
				font-size: 1.5rem;
			}
		}
		/*---------------------------------------------------------------------------------------------------------------------
// КОНТЕЙНЕР С ВЛОЖЕННЫМИ ЖЕЛТЫМИ ПЛИТКАМИ 
-----------------------------------------------------------------------------------------------------------------------*/

		&_tiles {
			border-radius: 8px;
			padding: 0 10px 10px;
			text-align: center;

			h3 {
				text-transform: uppercase;
				padding-top: 5px;
				padding-bottom: 5px;
				text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
				-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);
			}

			.tiles {
				display: grid;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 1fr 1fr;
				gap: 15px;
				// Вложенная плитка
				.tile {
					display: flex;
					flex-direction: column;
					justify-content: space-between;

					border-radius: 8px;
					min-height: 151px;
					position: relative;
					cursor: pointer;

					// Контейнер фонового изображения вложенной плитки
					&-goldpack_image {
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						z-index: 2;

						// Изображение вложенной плитки
						img {
							position: absolute;
							inset-block-start: 20%;
							inset-inline-start: 25%;
							object-fit: contain;
							block-size: 50%;
							inline-size: 50%;
							z-index: 0;
						}
					}

					// Подсветка КРУТЯШИЕСЯ ЖЕЛТЫЕ
					&-goldpack_rotate {
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						z-index: 2;

						// Изображение вложенной плитки
						img {
							position: absolute;
							inset-block-start: 0%;
							inset-inline-start: 0%;
							object-fit: contain;
							block-size: 100%;
							inline-size: 100%;
							z-index: 0;
						}
					}

					// ВЕРХНЯЯ ЛИНИЯ С ТЕКСТОМ
					&-goldpack_topline {
						position: relative;
						z-index: 5;
						padding-top: 5px;
						font-size: 1.6rem;
						text-align: center;
						text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
						-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);

						@include sm {
							font-size: 1.4rem;
						}
					}

					// НИЖНЯЯ ЛИНИЯ С КРИСТАЛОМ
					&-goldpack_bottomline {
						position: relative;
						z-index: 5;

						display: flex;
						justify-content: center;
						align-items: center;
						gap: 10px;

						background-color: rgb(0, 0, 0, 0.15);

						padding-top: 7px;
						padding-bottom: 7px;

						img {
							width: 28px;
							height: 28px;
						}

						span {
							font-size: 1.6rem;
							text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.5);
							-webkit-text-stroke: 1px rgba(0, 0, 0, 0.75);
						}
						@include sm {
							span {
								font-size: 1.4rem;
							}
						}
					}
				}
			}
		}
	}
}
