/* ----------------------------------------------------------------------------------------------------------------------
ПЕЧАТЬ ТЕКСТОМ
-----------------------------------------------------------------------------------------------------------------------*/
.stamp-text {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgb(0, 0, 0, 0.3);

	z-index: 2;

	&-inner {
		--size: 160px;

		@include md {
			--size: 140px;
		}

		@include sm {
			--size: 120px;
		}

		position: absolute;
		top: 50%;
		left: 50%;
		width: var(--size);
		height: var(--size);
		padding: 5px;

		background-image: url("../../images/stamp-empty.png");
		background-repeat: no-repeat;
		background-size: contain;
			
		transform: translate(-50%, -50%) rotate(-30deg) scale(0.8);

		// верхнее слово
		.stamp-top-word {
			position: absolute;
			top: 36px;
			left: 50%;
			transform: translateX(-50%);
			font-size: 1.4rem;
			font-weight: bold;

			color: var(--color-white);

			@include md {
				top: 32px;
				font-size: 1.1rem;
			}
			@include sm {
				top: 27px;
				font-size: 0.9rem;
			}
			transition: all 0.3s ease-in-out;
		}

		// среднее слово
		.stamp-middle-word {
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 2.1rem;
			line-height: 120%;
			color: var(--color-white);

			@include md {
				font-size: 1.7rem;
			}
			@include sm {
				font-size: 1.5rem;
			}

			transition: all 0.3s ease-in-out;

			border-top: 2px solid white;
			border-bottom: 2px solid white;
		}

		// нижнее слово
		.stamp-bottom-word {
			display: block;
			position: absolute;
			bottom: 36px;
			left: 50%;
			transform: translateX(-50%);
			font-size: 1.4rem;
			color: var(--color-white);

			@include md {
				bottom: 32px;
				font-size: 1.1rem;
			}
			@include sm {
				bottom: 27px;
				font-size: 0.9rem;
			}
			transition: all 0.3s ease-in-out;
		}
	}
}


/* ----------------------------------------------------------------------------------------------------------------------
ПЕЧАТЬ КАРТИНКОЙ
-----------------------------------------------------------------------------------------------------------------------*/
.stamp-image {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgb(0, 0, 0, 0.3);

	border-radius: 8px;
	overflow: hidden;

	z-index: 10;

	display: flex;
	align-items: center;
	justify-content: center;


	img {
		width: 100%;
		height: auto;
		transform: rotate(-30deg) scale(0.8);
	}
}
